@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&family=Roboto+Condensed:wght@300&display=swap);
:root {
  --primary-white: #fff;
  --primary-orange: #f9ab00;
  --primary-dark: rgba(255, 255, 255, 0);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
  --pink: #FD4370;
  --background-color: #141628;
  --fontHuge: 3rem;
  --fontSuperBig: 2rem;
  --fontBig: 1.2rem;
  --fontMed: 1rem;
  --fontSmall: 0.9rem;
}

body {
  font-family: 'Roboto Condensed', sans-serif;
}

/* NavBar */
nav {
  background-color: #141628 !important;
  background-color: var(--background-color) !important;
  z-index: 2;
}

nav a.navbar-brand {
  max-width: 17.25rem;
}

nav a.navbar-brand.logo {
  width: 16.25rem;
  font-family: 'Dancing Script', cursive;
  color: #fff;
  color: var(--primary-white);
  font-size: 2rem;
  font-size: var(--fontSuperBig);
}

nav a.navbar-brand:hover {
  color: #FD4370 !important;
  color: var(--pink) !important;
}


nav a.nav-link {
  letter-spacing: 0.0375rem;
  cursor: pointer;
}


nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin: 0.75rem 0; 
  color: #fff !important; 
  color: var(--primary-white) !important;
  font-size: 0.9rem;
  font-size: var(--fontSmall);
  border-top: 0.1875rem solid rgba(255, 255, 255, 0);
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-nav > li > a.active, .navbar-nav > li > a:focus {
  border-top: 0.1875rem solid #FD4370;
  border-top: 0.1875rem solid var(--pink);
  color: #FD4370 !important;
  color: var(--pink) !important;
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #FD4370;
  border-top: 0.1875rem solid var(--pink);
  color: #FD4370 !important;
  color: var(--pink) !important;
}

/* Header */
.header-wrapper {
  position: relative;
  /* background: url(software-development.jpg) no-repeat; */
  background: #141628;
  background: var(--background-color);
  background-size: cover;
  background-position: center;
  height: 90vh;
}

.main-info {
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: 'Roboto Condensed', sans-serif;
}

.main-info h1 {
  color: #fff;
  color: var(--primary-white);
  text-transform: uppercase;
  font-size: 3rem;
  font-size: var(--fontHuge);
}

/* Typed Text */
.typed-text {
  font-size: 2rem;
  font-size: var(--fontSuperBig);
  color: #fff;
  color: var(--primary-white);
}


/* Header Main Offer Button */
.btn-main-offer {
  border: 1px solid #FD4370;
  border: 1px solid var(--pink);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: #fff;
  color: var(--primary-white);
  margin-top: 2rem;
  z-index: 2;
  text-decoration: none;
  border-radius: 0.125rem;
}

.btn-main-offer:hover {
  background-color: #FD4370;
  background-color: var(--pink);
  color: #fff;
  color: var(--primary-white);
  transition: 0.2 ease-in-out;
}

/* Particles JS */
.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vh;
  z-index: 1;
}
